/* 
  ==============================================
     üéõÔ∏è .b Base Button | ÿØ⁄©ŸÖŸá Ÿæÿß€åŸá ÿ®ÿØŸàŸÜ ÿ±ŸÜ⁄Ø ÿÆÿßÿµ ÿ®ÿ±ÿß€å ÿßÿ≥ÿ™ŸÅÿßÿØŸá ÿπŸÖŸàŸÖ€å
  ============================================== 
*/
.b {
  padding: 15px 30px;
  border: none;
  font-size: 18px;
  cursor: pointer;
  transition: all 0.3s ease;
  border-radius: 8px;
  position: relative;
  overflow: hidden;
}
/* 
  ==============================================
     üîµ Button 1 Primary Ripple | ÿØ⁄©ŸÖŸá ÿßÿµŸÑ€å ÿ®ÿß ÿßŸÅ⁄©ÿ™ Ripple Ÿà ÿ≠ÿßŸÑÿ™‚ÄåŸáÿß€å ÿ™ÿπÿßŸÖŸÑ€å
  ============================================== 
*/
.btn1 {
  position: relative;
  overflow: hidden;
  display: inline-block;
  padding: 0.375rem 0.75rem;
  font-size: 1rem;
  line-height: 1.5;
  border-radius: 0.25rem;
  border: 1px solid transparent;
  cursor: pointer;
  transition: background-color 0.15s ease-in-out, filter 0.15s ease-in-out,
    box-shadow 0.15s ease-in-out;
  color: #fff;
  background-color: var(--blue);
  border-color: var(--blue);
  user-select: none;
  outline: none;
}
.btn1:hover {
  filter: brightness(0.9);
}
.btn1:active {
  filter: brightness(0.8);
}
.btn1:focus {
  box-shadow: 0 0 0 0.2rem rgba(0, 123, 255, 0.25);
}
.btn1::after {
  content: "";
  position: absolute;
  top: var(--y);
  left: var(--x);
  width: var(--size);
  height: var(--size);
  background: rgba(255, 255, 255, 0.5);
  border-radius: 50%;
  transform: scale(0);
  opacity: 1;
  transition: transform 0.6s ease-out, opacity 0.6s ease-out;
  pointer-events: none;
}
.btn1.active::after {
  transform: scale(4);
  opacity: 0;
}
.btn1:active::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 100%;
  padding-bottom: 100%;
  background: rgba(255, 255, 255, 0.5);
  border-radius: 50%;
  transform: translate(-50%, -50%) scale(0);
  opacity: 1;
  transition: transform 0.6s ease-out, opacity 0.6s ease-out;
  pointer-events: none;
}
.btn1:active::after {
  transform: translate(-50%, -50%) scale(4);
  opacity: 0;
  animation: ripple 0.6s ease-out;
}
@keyframes ripple {
  0% {
    transform: scale(0, 0);
    opacity: 1;
  }
  100% {
    transform: scale(20, 20);
    opacity: 0;
  }
}
/* 
  ==============================================
     üßº Button 2 Soft Shadow | ÿØ⁄©ŸÖŸá ÿ≥ŸÅ€åÿØ ÿ®ÿß ÿ≥ÿß€åŸá Ÿà ÿ™ÿ±ŸÜÿ≤€åÿ¥ŸÜ ÿ¨ÿßÿ®ÿ¨ÿß€å€å ŸÜÿ±ŸÖ
