/* 
  ==============================================
     ‚ö° Button 4 Shine Swipe | ÿØ⁄©ŸÖŸá ÿ®ÿ±ÿßŸÇ ÿ®ÿß ÿßŸÅ⁄©ÿ™ ÿ≠ÿ±⁄©ÿ™ ÿ±Ÿàÿ¥ŸÜÿß€å€å ÿØÿ± Hover
  ============================================== 
*/
